import styled, { css } from "styled-components";

import Button from "../../Button/Button";
import { colors, spacingMap } from "../../styleguide";
import { StyledModalProps } from "../Modal.types";

export const largeFooterDisplay = css`
	display: flex;
	justify-content: flex-end;
	gap: ${spacingMap.xs};
`;

export const defaultFooterDisplay = css`
	display: grid;
	grid-auto-columns: auto;
	grid-gap: ${spacingMap.xs};
`;

export const border = css`
	border-top: 1px solid ${colors.lightGray};
`;

export const FooterWrapper = styled.div<StyledModalProps>`
	padding: ${spacingMap.sm} ${spacingMap.md} ${spacingMap.md};
	${({ size }) => size === "large" ? largeFooterDisplay : defaultFooterDisplay};
	// Divider for scrollable variant is just a top border on the FooterWrapper
	${({ variant }) => variant === "scrollable" && border};
`;

export const StyledButton = styled(Button)`
	grid-row: 1;
`;

export const DarkSecondaryButton = styled(StyledButton)`
	background: ${colors.black};
	color: ${colors.white};
	border: 1px solid ${colors.white};
`;
